.card {
  display: flex;
  align-items: center;
  padding: 20px 18px 20px 14px;
  gap: 4px;
  position: relative;
}

.card:focus {
  background-color: var(--help-sidebar);
}

.cardActive {
  background-color: var(--help-sidebar);
}

.cardActive::after {
  content: '';
  position: absolute;
  right: 0;
  top: 0;
  border-radius: 4px 0 0 4px;
  width: 4px;
  height: 100%;
  background-color: var(--bg-add-user);
}

.cardSvg {
  width: 18px;
  height: 18px;
  flex-shrink: 0;
}

.btnSvg {
  width: 16px;
  height: 16px;
  color: var(--text-sidebar-rgb);
  transition: all 0.3s ease-in-out;
}

@media (hover: hover) and (pointer: fine) {
  .btnSvg:hover {
    color: var(--color-green-dark);
  }
}

.btnBlock {
  display: flex;
  gap: 8px;
}

.title {
  font-weight: 500;
  font-size: 14px;
  letter-spacing: -0.02em;
  color: var(--titel-sidebar);
  flex-grow: 1;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
